function Key(props) {
  const { context, value, params, onUpdate } = props
  // const { sources } = useContext(SearchContext)
  // const normalized = hydrateTree(value, params, sources, context)
  // const index = makeIndex(normalized)
  const errors = false //hasErrors(index)

  return (
    <KeyWrapper className={errors ? styles.errors : ''}>
      <Binding
        context={context}
        value={value}
        params={params}
        onUpdate={onUpdate}
      />
    </KeyWrapper>
  )
}

Key.propTypes = {
  label: PropTypes.string,
  value: bindPropTypes.value.isRequired,
  params: PropTypes.arrayOf(bindPropTypes.node),
  onUpdate: PropTypes.func.isRequired
}

export default Key
