import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'

function SuggestWrongRepo (props) {
  const { onDismiss, otherRepoOrBranchAvailable } = props
  return (
    <Modal>
      <DialogBox onDismiss={onDismiss}>
        <h2>Is this a zmk-config repo?</h2>
        <p>
          Hi there! The repository you're trying to load from doesn't have a
          defined <code>config</code> directory. Right now this application is
          expecting to look at that directory to discover keymaps and other
          metadata.
        </p>
        <p>
          I'm a humble developer and have only worked with configurations for my
          own handwired builds, so if this is a valid repo for zmk-powered
          keyboard firmware then please consider opening an issue on this
          project's GitHub repository to let me know what I can do differently.
        </p>
        {otherRepoOrBranchAvailable && (
          <p>
            If you have another branch or repository set up as your zmk-config
            repo you may switch to that instead.
          </p>
        )}
      </DialogBox>
    </Modal>
  )
}

SuggestWrongRepo.propTypes = {
  onDismiss: PropTypes.func.isRequired,
  otherRepoOrBranchAvailable: PropTypes.bool
}

export default SuggestWrongRepo
