import cloneDeep from 'lodash/cloneDeep'
import get from 'lodash/get'
import pick from 'lodash/pick'
import PropTypes from 'prop-types'
import { useContext, useState } from 'react'

import { SearchContext } from '../../providers'
import { getBehaviourParams } from '../../keymap'

import BindParamlist from './BindParamlist'
import * as bindPropTypes from './bindPropTypes'
import {
  createPromptMessage,
  hydrateTree,
  isSimple,
  isComplex,
  makeIndex
} from './util'
import styles from '../Keys/styles.module.css'

import Modal from '../../Common/Modal'
import ValuePicker from '../../ValuePicker'

function Binding (props) {
  const { getSearchTargets, sources } = useContext(SearchContext)
  const { context = 'none', hideBehaviour, value, params, onUpdate } = props
  const [editing, setEditing] = useState(null)

  const bind = value
  const behaviour = get(sources.behavioursByContext[context], bind)
  const behaviourParams = getBehaviourParams(params, behaviour)

  const normalized = hydrateTree(value, params, sources, context)

  const index = makeIndex(normalized)

  function handleSelectCode(event, key) {
    event.stopPropagation()
    const editing = pick(key, ['target', 'codeIndex', 'code', 'param'])
    editing.targets = getSearchTargets(editing.param, value, context)
    setEditing(editing)
  }
  function handleSelectBehaviour(event) {
    event.stopPropagation()
    setEditing({
      target: event.target,
      targets: getSearchTargets('behaviour', value, context),
      codeIndex: 0,
      code: value,
      param: 'behaviour'
    })
  }
  function handleSelectValue(source) {
    const { codeIndex } = editing
    const updated = cloneDeep(normalized)
    const index = makeIndex(updated)
    const targetCode = index[codeIndex]

    // Attempt to cast raw inputs to non-string type if such a type is defined
    // and I've specifically accounted for casting to that type here.
    // No error handling yet, so...
    if (editing.param.raw) {
      if (editing.param.type === 'number') {
        source.code = Number(source.code)
      }
    }
    else if (editing.param === 'command') {
      const oldAdditionalParams = targetCode.source?.additionalParams.length || 0
      const newAdditionalParams = source.additionalParams?.length || 0
      // When switching from a command parameter that has additionalParams to
      // another command parameter that has fewer (or none), the selected values
      // are still present in the tree but aren't rendered. Therefore it's still
      // possible for the unused values to be saved when the keymap is written.
      // This technically doesn't cause errors because behaviours are written to
      // ignore these values, but it can be confusing.
      // TODO: look into actually making sure that we aren't sending these
      // phantom values when submitting keymap state.
      if (newAdditionalParams < oldAdditionalParams) {
        // technically this could be anywhere in the tree but lets assume for
        // convenience that switching a "command" param can only happen to the
        // first child of the behaviour bind.
        const tree = index[0]
        tree.params.splice(1, tree.params.length - 1)
        index.splice(2, index.length - 1)
      }
    }

    targetCode.value = source.code

    // Clear parameters if the new source params list doesn't match the old one.
    // TODO: instead of a reduce and iterating through the entire array in all
    // cases, this could look for the first index where both params lists don't
    // match and then splice targetCode.params from there
    targetCode.params = targetCode.params.reduce((params, p, i) => {
      if (params.length === i && targetCode.source.params[i] === source.params[i]) {
        params.push(p)
      }
      return params
    }, [])

    index.forEach(node => {
      delete node.source
    })

    setEditing(null)
    onUpdate(pick(updated, ['value', 'params']))
  }

  return (
    <>
      {behaviour && !hideBehaviour ? (
        <span
          className={styles['behaviour-binding']}
          onClick={handleSelectBehaviour}
        >
          {behaviour.code}
        </span>
      ) : null}
      <BindParamlist
        root={true}
        index={index}
        params={behaviourParams}
        values={normalized.params}
        onSelect={handleSelectCode}
        wrapperProps={{
          'data-simple': isSimple(normalized),
          'data-long': isComplex(normalized, behaviourParams),
        }}
      />
      {editing && (
        <Modal>
          <ValuePicker
            target={editing.target}
            value={editing.code}
            freeForm={editing.param.raw}
            choices={editing.targets}
            prompt={createPromptMessage(editing.param)}
            searchKey="code"
            onSelect={handleSelectValue}
            onCancel={() => setEditing(null)}
          />
        </Modal>
      )}
    </>
  )
}

Binding.propTypes =  {
  context: PropTypes.string,
  hideBehaviour: PropTypes.bool.isRequired,
  value: bindPropTypes.value.isRequired,
  params: PropTypes.arrayOf(bindPropTypes.node),
  onUpdate: PropTypes.func.isRequired
}

Binding.defaultProps = {
  hideBehaviour: false
}

export default Binding
